import React, { Fragment } from "react";

export const Header = ({ form }) => {
  const formHeadings = [
    "",
    "Interest",
    "Players",
    "Physical",
    "Personal",
    "Financial",
    "Natural",
    "Cultural",
    "Visual",
  ];
  const createFormSteps = () => {
    const formSteps = [];
    for (let i = 1; i < 9; i++) {
      formSteps.push(
        i === form ? (
          <span key={`${formHeadings[i]}-active`}>
            <span className="formStepActive">{formHeadings[i]}</span>
            &#8192;
          </span>
        ) : (
          <span key={`${formHeadings[i]}`}>
            <span className="formStep">{formHeadings[i]}</span>
            &#8192;
          </span>
        )
      );
    }

    return formSteps;
  };

  const formHeaderOne =
    form > 0 ? (
      <Fragment>Directions: ....</Fragment>
    ) : (
      <Fragment>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod
        tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim
        veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea
        commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
        velit esse cillum dolore eu fugiat nulla pariatur.
      </Fragment>
    );

  const formSteps = form > 0 ? createFormSteps() : null;

  return (
    <div>
      <h1 id="headerTitle">
        Circle <br />
        Generator
      </h1>
      <div className="formSectionH1">{formHeaderOne}</div>
      <div id="formNavOne" className="formSectionNav">
        {formSteps}
      </div>
    </div>
  );
};
