import React, { useReducer, useState } from "react";
import { FormArea, Header } from "./components";
import { FormButtons } from "../../lib/FormButtons";
import {
  Landing,
  Results,
} from "./components/FormArea/components/ResponseArea/forms";
import { Row } from "react-bootstrap";

var currentPlayer = 0;
const initialState = { form: 0 };

const formReducer = (state, action) => {
  switch (action.type) {
    case "next":
      return { form: state.form + 1 };
    case "prev":
      return { form: state.form - 1 };
    case "reset":
      return { form: 0 };
    default:
      throw new Error("Form reducer has failed");
  }
};

export const Panel = ({ game, setGame }) => {
  const [state, formStep] = useReducer(formReducer, initialState);
  const [formInterest, setFormInterest] = useState("");

  const prevForm = () => {
    if (state.form === 9) {
      document.getElementById("nextButton").style.display = "inline-block";
      formStep({ type: "prev" });
    }
    formStep({ type: "prev" });
  };

  const nextForm = () => {
    if (state.form > 2) {
    }
    formStep({ type: "next" });
  };

  const intializeGameSession = () => {
    setGame({ ...game, inProgress: true });
    nextForm();
  };

  const setGameInterest = () => {
    setGame({ ...game, interest: formInterest.interest });
    nextForm();
  };

  const resetGame = () => {
    console.log("button was clicked");
    state.form !== 9 ? prevForm() : formStep({ type: "reset" });
    setGame({
      inProgress: false,
      playerNames: [],
      playersProfiles: [],
    });
    document.getElementById("nextButton").style.display = "inline-block";
  };

  if (state.form === 0) {
    return (
      <div className="panelSection">
        <Row className="panelHeaderRow">
          <Header />
        </Row>
        <Row className="panelFormAreaRow">
          <Landing intializeGameSession={intializeGameSession} />
        </Row>
      </div>
    );
  } else if (state.form === 9) {
    return (
      <div className="panelSection">
        <Row className="panelHeaderRow">
          <Header />
        </Row>
        <Row className="panelFormAreaRow">
          <Results resetGame={resetGame} />
        </Row>
        <Row className="panelButtonRow">
          <FormButtons
            resetGame={state.form === 1 ? resetGame : null}
            prevForm={prevForm}
            nextForm={nextForm}
          />
        </Row>
      </div>
    );
  }
  return (
    <div className="panelSection">
      <Row className="panelHeaderRow">
        <Header form={state.form} />
      </Row>
      <Row className="panelFormAreaRow">
        <FormArea
          game={game}
          setGame={setGame}
          formInterest={formInterest}
          setFormInterest={setFormInterest}
          currentPlayer={currentPlayer}
          form={state.form}
        />
      </Row>
      <Row className="panelButtonRow">
        <FormButtons
          resetGame={state.form === 1 ? resetGame : null}
          prevForm={prevForm}
          nextForm={nextForm}
          setGameInterest={state.form === 1 ? setGameInterest : null}
        />
      </Row>
    </div>
  );
};
