import React from "react";
import {
  InterestForm,
  PlayerForm,
  OneForm,
  TwoForm,
  ThreeForm,
  FourForm,
  FiveForm,
  SixForm,
  Results,
} from "./forms";

export const ResponseArea = ({
  form,
  formData,
  setFormData,
  formInterest,
  setFormInterest,
}) => {
  switch (form) {
    case 1:
      return (
        <InterestForm
          formInterest={formInterest}
          setFormInterest={setFormInterest}
        />
      );
    case 2:
      return <PlayerForm formData={formData} setFormData={setFormData} />;
    case 3:
      return <OneForm formData={formData} setFormData={setFormData} />;
    case 4:
      return <TwoForm formData={formData} setFormData={setFormData} />;
    case 5:
      return <ThreeForm formData={formData} setFormData={setFormData} />;
    case 6:
      return <FourForm formData={formData} setFormData={setFormData} />;
    case 7:
      return <FiveForm formData={formData} setFormData={setFormData} />;
    case 8:
      return <SixForm formData={formData} setFormData={setFormData} />;
    case 9:
      return <Results />;
    default:
      throw new Error("FormArea switch has failed");
  }
};
