import React from "react";
import { Row, Col } from "react-bootstrap";

export const FormButtons = ({
  prevForm,
  nextForm,
  setGameInterest,
  resetGame,
}) => {
  return (
    <div id="formButtons" className="col-11 text-center">
      <Row>
        <Col>
          <button
            className="formBtn"
            id="prevButton"
            onClick={() => (resetGame !== null ? resetGame() : prevForm())}
          >
            <span>
              Previous <br /> Form
            </span>
          </button>
        </Col>
        <Col>
          <button
            type="submit"
            className="formBtn"
            id="nextButton"
            onClick={() =>
              setGameInterest !== null ? setGameInterest() : nextForm()
            }
          >
            <span>
              {" "}
              Submit <br /> Form
            </span>
          </button>
        </Col>
      </Row>
    </div>
  );
};
