import React, { useState, useReducer, Fragment } from "react";
import { Form, FormGroup } from "react-bootstrap";
import { ResponseArea } from "./components";
import { PlayerButtons } from "../../../../lib/PlayerButtons";
import { Player } from "../../../../lib/Player";
import { useEffect } from "react";

const initialState = { player: 0 };

const playerReducer = (state, action) => {
  switch (action.type) {
    case "next":
      return { player: state.player + 1 };
    case "prev":
      return { player: state.player - 1 };
    case "reset":
      return { player: 0 };
    default:
      throw new Error("Count reducer has failed");
  }
};

export const FormArea = ({
  form,
  game,
  setGame,
  formInterest,
  setFormInterest,
}) => {
  const [state, playerStep] = useReducer(playerReducer, initialState);
  const [formData, setFormData] = useState({});
  const playerNames = Object.values(game.playerNames);

  useEffect(() => {
    if (form === 2) {
      if (state.player !== 0) {
        document.getElementById("prevPlayerButton").className = "playerBtn";
      } else {
        document.getElementById("prevPlayerButton").className =
          "playerBtnDisabled";
      }
      if (state.player === 4) {
        document.getElementById("nextPlayerButton").className =
          "playerBtnDisabled";
      } else {
        document.getElementById("nextPlayerButton").className = "playerBtn";
      }
    }
    if (form > 2) {
      if (state.player !== 0) {
        document.getElementById("prevPlayerButton").className = "playerBtn";
      } else {
        document.getElementById("prevPlayerButton").className =
          "playerBtnDisabled";
      }

      if (form > 2) {
        if (state.player === playerNames.length) {
          document.getElementById("nextPlayerButton").className =
            "playerBtnDisabled";
        } else {
          document.getElementById("nextPlayerButton").className = "playerBtn";
        }
      }
    }
  }, [state.player, form, playerNames.length]);

  useEffect(() => {
    playerStep({ type: "reset" });
  }, [form]);

  const nextPlayer = () => {
    playerStep({ type: "next" });
  };

  const prevPlayer = () => {
    playerStep({ type: "prev" });
  };

  const createPlayerIcons = (inital) => {
    const playerIcons = [];
    if (inital === true) {
      for (let i = 0; i < 5; i++) {
        playerIcons.push(
          i === state.player ? (
            <span className="stepIconActive" key={i}></span>
          ) : (
            <span className="stepIcon" key={i}></span>
          )
        );
      }
    } else {
      for (let i = 0; i < playerNames.length; i++) {
        playerIcons.push(
          i === state.player ? (
            <span className="stepIconActive" key={i}></span>
          ) : (
            <span className="stepIcon" key={i}></span>
          )
        );
      }
    }

    return playerIcons;
  };

  const createPlayerProfiles = () => {
    var player;
    if (form === 2) {
      player = new Player(formData.name, formData.association);
      return player;
    }
  };

  const updatePlayerProfiles = () => {
    Object.keys(formData).forEach((key, i) => {
      game.playerProfiles[state.player][key] = formData[key];
    });
  };

  const onSubmit = (event) => {
    event.preventDefault();
    if (Object.keys(formData).length === 0) {
      console.log("no data");
      return;
    }

    if (form === 2) {
      setGame({
        ...game,
        playerNames: { ...game.playerNames, [state.player]: formData.name },
        playerProfiles: {
          ...game.playerProfiles,
          [state.player]: createPlayerProfiles(),
        },
      });
    } else {
      updatePlayerProfiles();
    }
    document.getElementById(`inputForm`).reset();
    nextPlayer();
    console.log("Submit ran");
  };

  const currentPlayerDisplay =
    form > 2 && form < 9 ? (
      <div className="col-11 text-center" id="currentPlayer">
        {game.playerNames[state.player]}
      </div>
    ) : (
      <div className="col-11" id="currentPlayer"></div>
    );

  const formAreaDisplay = () => {
    if (state.player === playerNames.length && form > 2) {
      return (
        <FormGroup className="text-center">
          Click
          <br /> "Submit Form"
          <br /> to
          <br /> continue
        </FormGroup>
      );
    } else {
      return (
        <ResponseArea
          form={form}
          formData={formData}
          setFormData={setFormData}
          interest={formInterest}
          setFormInterest={setFormInterest}
        />
      );
    }
  };
  const playerIconsAndButtons =
    form !== 1 ? (
      <Fragment>
        <FormGroup className="text-center">
          <div id="stepIconRow">
            {createPlayerIcons(form === 2 ? true : false)}
          </div>
        </FormGroup>
        <FormGroup className="text-center">
          <PlayerButtons prevPlayer={prevPlayer} nextPlayer={nextPlayer} />
        </FormGroup>
      </Fragment>
    ) : (
      <Fragment></Fragment>
    );

  return (
    <Fragment>
      {currentPlayerDisplay}
      <div className="col">
        <div className="col-11" id="formArea">
          <Form id="inputForm" autoComplete="off" onSubmit={onSubmit}>
            {formAreaDisplay()}
            {playerIconsAndButtons}
          </Form>
        </div>
      </div>
    </Fragment>
  );
};
