function polarGrid(display) {
  const gridLines = [];
  gridLines.push(
    <line
      key={"Axis: -X, Y"}
      x1={display.cx - display.cross}
      y1={display.cy - display.cross}
      x2={display.cx + display.cross}
      y2={display.cy + display.cross}
      stroke="black"
      strokeWidth={0.5}
    />,
    <line
      key={"Axis: X, -Y"}
      x1={display.cx + display.cross}
      y1={display.cy - display.cross}
      x2={display.cx - display.cross}
      y2={display.cy + display.cross}
      stroke="black"
      strokeWidth={0.5}
    />,
    <line
      key={"Axis: X"}
      x1={display.cx - display.axis}
      y1={display.cy}
      x2={display.cx + display.axis}
      y2={display.cy}
      stroke="black"
      strokeWidth={0.5}
    />,
    <line
      key={"Axis: Y"}
      x1={display.cx}
      y1={display.cy - display.axis}
      x2={display.cx}
      y2={display.cy + display.axis}
      stroke="black"
      strokeWidth={0.5}
    />
  );

  return gridLines;
}

function setBlueRings(display) {
  const blueRings = [];
  let y = display.step / 2;
  for (let i = 0; i < 15; i++) {
    y = y + display.step;

    blueRings.push(y);
  }
  return blueRings;
}

function bluePolarRings(display) {
  const blueRings = setBlueRings(display);
  const blueDisplay = [];
  blueRings.forEach((r, i) => {
    blueDisplay.push(
      <circle
        key={`Blue Ring: ${i}`}
        cx={display.cx}
        cy={display.cy}
        r={r}
        stroke="rgba(65,105,225)"
        strokeWidth={1}
        fill="none"
        shapeRendering="geometricPrecision"
      />
    );
  });

  return blueDisplay;
}

function setDarkRings(display) {
  const darkRings = [2];
  let point = 0;
  for (let i = 0; i < 16; i++) {
    point = point + display.step;

    darkRings.push(point);
  }
  return darkRings;
}

function darkPolarRings(display) {
  const darkRings = setDarkRings(display).reverse();
  const darkDisplay = [];
  darkRings.forEach((r, i) => {
    if (r === 2) {
      darkDisplay.push(
        <circle
          key={`Dark Ring: ${i}`}
          cx={display.cx}
          cy={display.cy}
          r={r}
          fill="black"
          shapeRendering="geometricPrecision"
        />
      );
    } else if (r < 400) {
      darkDisplay.push(
        <circle
          key={`Dark Ring: ${i}`}
          cx={display.cx}
          cy={display.cy}
          r={r}
          stroke="black"
          strokeWidth={1}
          fill="none"
          shapeRendering="geometricPrecision"
        />
      );
    } else {
      darkDisplay.push(
        <circle
          key={`Dark Ring: ${i}`}
          cx={display.cx}
          cy={display.cy}
          r={r}
          stroke="black"
          strokeWidth={1}
          fill="rgba(187, 192, 156, 0.52)"
          shapeRendering="geometricPrecision"
        />
      );
    }
  });

  return darkDisplay;
}

export class Canvas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toUpdate: false,
      view: {
        height: window.innerHeight,
        width: window.innerWidth,
      },
      display: {
        svgDim: 0,
        radius: 0,
        axis: 0,
        cross: 0,
        cx: 0,
        cy: 0,
        step: 0,
      },
    };
  }

  componentDidMount() {
    console.log("mounted");
    this.setState({
      view: { height: window.innerHeight, width: window.innerWidth },
    });
    this.handleDisplayChanges();
  }

  componentDidUpdate() {
    var delay;
    window.addEventListener("resize", () => {
      clearTimeout(delay);
      delay = setTimeout(() => {
        this.setState({
          view: { height: window.innerHeight, width: window.innerWidth },
        });
        this.handleDisplayChanges();
      }, 250);
    });
  }

  componentWillUnmount() {
    console.log("unmounted");
  }

  handleDisplayChanges() {
    let axis =
      this.state.display.radius === 0
        ? this.state.view.height * 0.41 * 1.08
        : this.state.display.radius * 1.08;
    let cross = this.state.view.height * 0.41 * 1.08 * 0.7;

    let center =
      this.state.display.svgDim === 0
        ? (this.state.view.height * 0.95) / 2
        : this.state.display.svgDim / 2;
    let step =
      this.state.display.radius === 0
        ? (this.state.view.height * 0.41) / 16
        : this.state.display.radius / 16;

    this.setState({
      display: {
        svgDim: this.state.view.height * 0.95,
        radius: this.state.view.height * 0.41,
        axis: axis,
        cross: cross,
        cx: center,
        cy: center,
        step: step,
      },
    });
  }

  render() {
    if (!this.props.game.inProgress) {
      return null;
    }
    return (
      <div className="svgDisplay">
        <svg
          height={this.state.display.svgDim}
          width={this.state.display.svgDim}
        >
          {bluePolarRings(this.state.display)}
          {darkPolarRings(this.state.display)}
          {polarGrid(this.state.display)}
        </svg>
      </div>
    );
  }
}
