import React from "react";
import { FormGroup, FormLabel, FormControl } from "react-bootstrap";

export const TwoForm = ({ formData, setFormData }) => {
  const handleTime = (event) => {
    setFormData({ ...formData, time: event.target.value });
  };

  const handlePersonality = (event) => {
    setFormData({ ...formData, personality: event.target.value });
  };

  const handleHair = (event) => {
    setFormData({ ...formData, hair: event.target.value });
  };

  return (
    <FormGroup>
      <FormLabel>Time</FormLabel>
      <FormControl
        onChange={handleTime}
        id="formTemporal"
        as="select"
        defaultValue={"DEFAULT"}
      >
        <option value="DEFAULT">Choose...</option>
        <option value="18">Morning</option>
        <option value="45">Evening</option>
      </FormControl>
      <FormLabel>Personality</FormLabel>
      <FormControl
        onChange={handlePersonality}
        id="formPersonality"
        as="select"
        defaultValue={"DEFAULT"}
      >
        <option value="DEFAULT">Choose...</option>
        <option value="60">Introvert</option>
        <option value="31">Extrovert</option>
      </FormControl>
      <FormLabel>Hair</FormLabel>
      <FormControl
        onChange={handleHair}
        id="formHair"
        as="select"
        defaultValue={"DEFAULT"}
      >
        <option value="DEFAULT">Choose...</option>
        <option value="10">Curly</option>
        <option value="18">Straight</option>
        <option value="25">Wavy</option>
      </FormControl>
    </FormGroup>
  );
};
