import React, { useState } from "react";
import { render } from "react-dom";
import { Panel, Canvas } from "./components";
import "bootstrap/dist/css/bootstrap.min.css";
import "./app.css";
import { Container, Row, Col } from "react-bootstrap";

const App = () => {
  const [game, setGame] = useState({
    inProgress: false,
    playerNames: {},
    playerProfiles: {} || null,
    form: 0,
  });

  return (
    <Container fluid className="App">
      <Row id="displayRow" className="displayArea">
        <Col xl={3} xs={4} id="panelSide" className="panel">
          <Panel game={game} setGame={setGame} />
        </Col>
        <Col id="canvasSide" className="canvas text-center">
          <Canvas game={game} setGame={setGame} />
        </Col>
      </Row>
    </Container>
  );
};

render(<App />, document.getElementById("root"));
